import "./App.css";

import Search from "./components/Search";
import Data from "./components/Data";
import Map from "./components/Map";
import { useState, useEffect } from "react";

function App() {
  const [searchIp, setsearchIp] = useState([]);
  const [ipAddress, setIpAddress] = useState([]);

  const handleIpSubmit = (e) => {
    e.preventDefault();
    setsearchIp({
      [e.target.name]: e.target.value,
    });
    URLAddress();
    console.log(ipAddress);
  };

  const URL = `https://geo.ipify.org/api/v2/country,city?apiKey=at_N6BJYFRznP7wvvUanJZu2Pmrvn2l8&ipAddress=${
    searchIp.ipAddress || "192.212.174.101"
  }`;

  const URLAddress = () => {
    fetch(URL)
      .then((resp) => resp.json())
      .then((data) => {
        setIpAddress(data);
      })
      .catch((error) => {
        console.log(error);
        return <h1>Coloca algo valido porfavor</h1>;
      });
  };

  useEffect(() => {
    URLAddress();
  }, []);

  return (
    <div className="APP-back flex flex-col items-center justify-center ">
      <div className="backgroundImage h-80 w-full flex flex-col items-center pt-4">
        <h1 className="text-white text-xl font-medium">IP Address Tracker</h1>

        <Search
          searchIp={searchIp.ip} //for btn logic
          ipAddress={ipAddress.ip}
          handleIpSubmit={handleIpSubmit}
        />
        <Data
          ipAddress={ipAddress.ip}
          location={ipAddress.location}
          isp={ipAddress.isp}
        />
      </div>

      <Map
        lat={ipAddress.location.lat || 51.505}
        lng={ipAddress.location.lat || -0.09}
      />
    </div>
  );
}

export default App;
