import React from "react";

const Data = ({ ipAddress, location, isp }) => {
  return (
    <div className=" z-50 absolute top-32 rounded-lg mt-8 bg-white w-10/12 p-2 px-3 text-center ">
      <div className="ip">
        <span className="ip-spanm_text">IP ADDRESS</span>
        <h1 className="ip-data"> {ipAddress} </h1>
      </div>
      <div className="ip">
        <span className="ip-spanm_text">LOCATION</span>
        <h1 className="ip-data">
          {" "}
          {location.city}, {location.country} {location.postalCode}
        </h1>
      </div>
      <div className="ip">
        <span className="ip-spanm_text">TIMEZONE</span>
        <h1 className="ip-data"> UTC {location.timezone} </h1>
      </div>
      <div className="ip">
        <span className="ip-spanm_text">ISP</span>
        <h1 className="ip-data"> {isp} </h1>
      </div>
    </div>
  );
};

export default Data;
